<template>
  <div class="mapTable" >
    <div class="modelBox">
      <p>
        <strong :class="{ active : tabState }" @click="switchTab(1)">
          预设界桩
        </strong>
        <strong :class="{ active : !tabState }" @click="switchTab(0)">
          统计
        </strong>
      </p>
      <div class="box" :style="{height: tableHeight+'px'}">
        <div class="tab_box" v-if="tabState"  style="border: 0;border-top: 1px solid #d1d1d1">
          <div style="text-align:right;margin:5px 8px" >
            <Button type="primary" size="small" @click="exportPreExcel">导出</Button>
            <!--<Button type="primary" size="small" style="margin:0 8px">导入</Button>-->
          </div>
          <Table :loading="loading" stripe :columns="columns1" :data="data1" size="small" v-show="telescopic" :height="tableHeight - 34" highlight-row  style="margin-right: -1px"></Table>
        </div>
        <div class="tab_box" v-if="!tabState" style="border-top:1px #d1d1d1 solid;">
          <VerticalBar></VerticalBar>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getPreMarkerList, exportPreExcel } from '@/api/acquisitionTable'
import { getToken } from '@/utils/auth'
import VerticalBar from './Tablet_verticalBar'
export default {
  name: 'MapTable',
  data () {
    return {
      /* 表格高度 */
      tableHeight: window.innerHeight - 272,
      /* 列表伸缩框打开 */
      telescopic: true,
      /* tab选项卡状态 */
      tabState: 1,
      /* 登记表框状态 */
      registerTab: false,
      /* 登记表查询id */
      tabletId: '',
      /* 原始界桩表头 */
      columns1: [
        {
          title: '界桩编码',
          key: 'jzNumber',
          align: 'center',
          width: 180,
          fixed: 'left',
          render: (h, params) => {
            return h('div', [
              h('a', {
                style: {
                  color: '#2d8cf0',
                  textDecoration: 'underline'
                },
                on: {
                  click: () => {
                    this.ondbclickTd(params.row.longitude, params.row.latitude, params.row.id)
                  }
                }
              }, params.row.jzNumber)
            ])
          }
        },
        {
          title: '界桩刻号',
          key: 'jzKh',
          align: 'center',
          minWidth: 80
        },
        {
          title: '所属红线斑块',
          key: 'redlineName',
          align: 'center',
          minWidth: 170
        },
        {
          title: '行政区划',
          key: 'placeName',
          align: 'center',
          minWidth: 150
        }
      ],
      /* 表格数据 */
      data1: [],
      /* 页码 */
      pageNum: 1,
      /* 每页条数 */
      pageSize: 100,
      /* 总条数 */
      total: 0,
      /* 加载 */
      loading: false,
      /* 懒加载按钮显示状态 */
      showBtn: false
    }
  },
  props: {
    obj: {}
  },
  components: {
    VerticalBar
  },
  watch: {
    obj () {
      this.data1 = []
      this.getTableData()
    },
    pageNum () {
      this.getTableData()
    },
    spotTable (val) {
      this.$emit('mapSizeChange', val)
    }
  },
  created () {
    this.getTableData()
  },
  mounted () {
    /* 监听浏览器高度 */
    window.onresize = function () {
      this.tableHeight = window.innerHeight - 272
    }.bind(this)
    /* 监听滑动事件 */
    document.getElementsByClassName('ivu-table-body')[0].addEventListener('scroll', () => {
      let ivuTableBody = document.getElementsByClassName('ivu-table-body')[0]
      // 变量scrollTop是滚动条滚动时，距离顶部的距离
      let scrollTop = ivuTableBody.scrollTop
      // 变量windowHeight是可视区的高度
      let windowHeight = ivuTableBody.clientHeight
      // 变量scrollHeight是滚动条的总高度
      let scrollHeight = ivuTableBody.scrollHeight
      // 滚动条到底部的条件
      if (scrollTop + windowHeight + 50 > scrollHeight) {
        /* this.getTableData() */
        if (this.total / this.pageSize >= this.pageNum) {
          this.pageNum++
        }
      }
    })
  },
  methods: {
    /* 获取预设界桩列表 */
    getTableData () {
      this.loading = true
      getPreMarkerList({
        accessToken: getToken(),
        validate: true,
        data: {
          code: this.obj.code,
          param: this.obj.param,
          pageNum: this.pageNum,
          pageSize: this.pageSize
        }
      }).then((res) => {
        this.data1 = this.data1.concat(res.data.data.rows)
        this.total = res.data.data.total
        this.$emit('getPointData', res.data.data.rows)
        this.loading = false
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 双击控制地图跳转经纬度 */
    ondbclickTd (lon, lat, id) {
      this.$emit('ondbclickDingwei', lon, lat, id)
    },
    /* 分页跳转 */
    pageChange (e) {
      this.pageNum = e
    },
    /* 切换界桩和统计 */
    switchTab (state) {
      this.tabState = state
      this.registerTab = false
    },
    /* 导出预设界桩界桩列表文件 */
    exportPreExcel () {
      exportPreExcel({
        data: {
          code: this.obj.code,
          param: this.obj.param
        }
      }).then((res) => {
        let data = res.data
        if (!data) {
          return
        }
        let link = document.createElement('a')
        link.style.display = 'none'
        let url = res.data.data.filepath
        link.href = url
        link.setAttribute('download', url.split('/')[url.split('/').length - 1])
        document.body.appendChild(link)
        link.click()
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    }
  }
}
</script>

<style lang="scss" scoped rel="stylesheet/scss">
  .mapTable {
    height: 100%;
    .btnBox {
      position: absolute;
      right: 10px;
      top: 10px;
    }
    .modelBox {
      background: #F5F5F5;
      height: 100%;
      p {
        padding: 0 15px 0 0;
        position: relative;
        overflow: hidden;
        text-align: left;
        strong {
          float: left;
          display: inline-block;
          cursor: pointer;
          height: 100%;
          font-size: 14px;
          padding: 6px 20px;
        }
        .active {
          border: 1px solid #DADCE0;
          background: #fff;
          border-bottom: 0;
        }
        span {
          float: right;
          cursor: pointer;
        }
      }

      .box {
        position: relative;
        background: #fff;
        overflow: hidden;
        padding: 0 0 0 5px;
        .tab_box{
          position: absolute;
          top: 0;
          left: 0;
          width: calc(100% - 6px);
          background: #fff;
          height: 100%;
          margin-left: 6px;
          border-top:1px solid #DADCE0;
        }
        .table_box{
          position: absolute;
          top: 0;
          left: 100%;
          width: calc(100% - 6px);
          background: #fff;
          height: 100%;
          z-index: 13131313;
          margin-left: 6px;
          border-top:1px solid #DADCE0;
          overflow: auto;
          transition: all 0.8s ease;
        }
        .regActive{
          top: 0;
          left: 0;
        }
        .addBtn{
          position: absolute;
          left: -37px;
          bottom: 25px;
          width: 44px;
          z-index: 16513;
          background: #D8E6EF;
          border: #fff solid 4px;
          box-shadow: 0 0 0 1px #dcdcdc;
          padding: 3px;
          outline: none;
          cursor: pointer;
        }
      }
    }
  }
</style>
