<template>
  <div id="tablet">
    <QueryBox @searchSrldId="searchSrldId"></QueryBox>
    <div class="demo-split">
      <Split v-model="split1" min="200px" max="600px">
        <div slot="left" class="demo-split-pane">
          <Map  ref="map1" :pointData="pointData"></Map>
        </div>
        <div slot="right" class="demo-split-pane">
          <PresetTabletTab ref="tab" :obj="obj" @ondbclickDingwei="ondbclickDingwei"  @getPointData="getPointData" @mapSizeChange="mapSizeChange"></PresetTabletTab>
        </div>
      </Split>
    </div>
  </div>
</template>

<script>
import Map from '../map/PresetTablet_map.vue'
import PresetTabletTab from './PresetTablet_tab.vue'
import QueryBox from './Tablet_query'
export default {
  name: 'Tablet',
  data () {
    return {
      /* 查询框返回查询条件 */
      obj: {},
      pointData: [],
      lon: '',
      lat: '',
      spotTable: false,
      split1: 0.6
    }
  },
  components: {
    Map,
    PresetTabletTab,
    QueryBox
  },
  mounted () {
  },
  methods: {
    /* 双击表格修改选中经纬度 */
    ondbclickDingwei (lon, lat, id) {
      this.$refs.map1.ondbclickDingwei(lon, lat, id)
    },
    /* 级联查询返回要搜索的id */
    searchSrldId (obj) {
      this.obj = obj
    },
    /* 获取全部界桩数据 */
    getPointData (data) {
      this.pointData = data
    },
    /* 控制地图框缩小 */
    mapSizeChange (spotTable) {
      if (spotTable === true) {
        this.spotTable = true
      } else {
        this.spotTable = false
      }
    }
  }
}
</script>

<style lang='scss' scoped rel='stylesheet/scss'>
  #tablet{
    position: relative;
    .map_box{
      position:absolute;
      width:60%;
      top: 55px;
      bottom: 0;
    }
    .cascader_box{
      position: absolute;
      left:70px;
      top:70px;
    }
    .spotTableBox{
      position: absolute;
      right:0;
      top:55px;
      width:40%;
    }
    .demo-split{
      height: calc(100% - 54px);
    }
    .demo-split-pane{
      height: 100%;
    }
  }
</style>
