<template>
  <div style="height: 100%">
    <div id="viewDiv">
    </div>
    <!-- 地图的工具框 -->
    <ButtonGroup id="toolbar" vertical style="display: flex;flex-direction: column">
      <Tooltip content="距离测量" placement="right">
        <Button :type="type=='distance'?'primary':'default'" @click="clickToolbar('distance')" style="padding:5px 5px 0px">
          <img src="@/assets/map_images/length.png" alt="" style="width: 20px;height: 20px;">
        </Button>
      </Tooltip>
      <Tooltip content="测量面积" placement="right">
        <Button :type="type=='area'?'primary':'default'" @click="clickToolbar('area')" style="padding:5px 5px 0px">
          <img src="@/assets/map_images/area.png" alt="" style="width: 20px;height: 20px;">
        </Button>
      </Tooltip>
    </ButtonGroup>
    <!-- 图例 -->
    <div id="legendbar">
      <Legend :data="legendData"></Legend>
    </div>
  </div>
</template>

<script>
import config from '../../setting/mapConfig'
import arcgisPackage from '../../utils/arcgisTools'
import { tdtlayer, tdtyNoteslayer, tdtclayer, tdtcNoteslayer, redLine, shengjie } from '@/utils/layers'
import presetBm3 from '../../assets/boundaryMarker_images/preset_bm3.png'
import collSign3 from '../../assets/boundaryMarker_images/coll_sign3.png'
import Basemapthumbnail from '../../assets/map_images/Basemapthumbnail.png'
import Basemapthumbnail1 from '../../assets/map_images/Basemapthumbnail1.png'
import Legend from '@/components/Legend'

export default {
  name: 'TabletMap',
  data () {
    return {
      activeWidget: null, // 当前激活的测量工具
      type: '', // 正在使用的测量工具标识
      legendData: [
        {
          type: 0,
          name: '红线斑块',
          backgroundColor: 'rgba(255, 255, 255, 0)',
          border: '2px solid red',
          id: '0',
          switch: true,
          switchDis: true
        },
        {
          type: 1,
          name: '预设界桩',
          background: 'url(' + presetBm3 + ')',
          id: 'inflectionPoint',
          switch: true,
          switchDis: true
        }
      ],
      popupTemplate: {}, // 点击地图中点的弹框对象
      loadComplete: false // 地图加载完成状态
    }
  },
  props: {
    pointData: {}
  },
  components: {
    Legend
  },
  watch: {
    legendData: {
      handler (val) {
        for (let i in val) {
          if (val[i].switchDis) {
            this.map.findLayerById(val[i].id).visible = val[i].switch
          }
        }
      },
      immediate: false,
      deep: true
    }
  },
  async mounted () {
    await this.createMap()
    this.addPointToMap(this.pointData, config.inflectionPointcolor, 'inflectionPoint')
    this.$watch('pointData', (val) => {
      this.addPointToMap(val, config.inflectionPointcolor, 'inflectionPoint')
    })
  },
  methods: {
    async createMap () {
      // 导入需要的arcgis模块
      let MapView = await arcgisPackage.MapView
      let Map = await arcgisPackage.Map
      let Basemap = await arcgisPackage.Basemap
      let BasemapToggle = await arcgisPackage.BasemapToggle
      let Fullscreen = await arcgisPackage.Fullscreen

      // 天地图影像地图
      let tdtylayer = await tdtlayer().then(res => { return res })
      // 天地图矢量地图
      let tdtylayer1 = await tdtclayer().then(res => { return res })
      // 天地图的矢量标注图层
      let tdtylayer2 = await tdtcNoteslayer().then(res => { return res })
      // 天地图影像标注图层
      let tdtylayer3 = await tdtyNoteslayer().then(res => { return res })

      // 省界
      let layerProvince = await shengjie().then(res => { return res })
      // 红线斑块图层
      let layerRedLine = await redLine().then(res => { return res })

      let customBasemap = new Basemap({
        baseLayers: [tdtylayer1, layerProvince, tdtylayer2],
        title: '矢量地图',
        id: 'myBasemap',
        thumbnailUrl: Basemapthumbnail
      })
      let customBasemap1 = new Basemap({
        baseLayers: [tdtylayer, layerProvince, tdtylayer3],
        title: '影像地图',
        id: 'myBasemap1',
        thumbnailUrl: Basemapthumbnail1
      })

      this.map = new Map({
        basemap: customBasemap1
      })
      this.view = new MapView({
        map: this.map,
        container: 'viewDiv',
        center: config.centerPoint,
        zoom: 7
      })
      this.view.when(wes => {
        this.loadComplete = true
      })
      this.popupTemplate = {
        title: '{name}',
        content: [
          {
            type: 'fields',
            fieldInfos: [
              {
                fieldName: 'jzNumber',
                label: '界桩编码'
              },
              {
                fieldName: 'placeName',
                label: '行政区划'
              },
              {
                fieldName: 'longitude',
                label: '经度'
              },
              {
                fieldName: 'latitude',
                label: '纬度'
              }
            ]
          }
        ]
      }
      this.map.layers.add(layerRedLine)
      // 添加全屏
      let fullscreen = new Fullscreen({ view: this.view })
      this.view.ui.add(fullscreen, 'top-left')
      // 增加底图切换插件
      let basemapToggle = new BasemapToggle({
        view: this.view,
        nextBasemap: customBasemap
      })
      this.view.ui.add(basemapToggle, 'top-right')
      // 添加工具框
      this.view.ui.add(document.getElementById('toolbar'), 'top-right')
      // 添加图例
      this.view.ui.add(document.getElementById('legendbar'), 'bottom-left')
      let highlight
      // 创建layer查询
      const query = layerRedLine.createQuery()
      let layerView = await this.view.whenLayerView(layerRedLine)
      // 添加点击事件
      this.view.on('click', (event) => {
        this.view.hitTest(event).then(response => {
          highlight && highlight.remove()
          // 过滤只要红线板块的图层
          if (response.results.length) {
            const graphic = response.results.filter(result => {
              return result.graphic.layer === layerRedLine
            })[0].graphic

            let id = graphic.attributes.FID
            query.where = 'FID = ' + id
            layerView.queryObjectIds(query).then(ids => {
              highlight = layerView.highlight(ids)
            })
          }
        })
      })
    },
    /**
     * 添加界桩
     * @param pointArray 点数组
     * @param symbolColor 符号颜色
     * @param layerId 自定义图层id
     */
    async addPointToMap (pointArray, symbolColor, layerId) {
      let GraphicsLayer = await arcgisPackage.GraphicsLayer
      let Graphic = await arcgisPackage.Graphic
      let layer = this.map.findLayerById(layerId)
      let point = {}
      let pointGraphic
      if (layer) {
        this.map.remove(layer)
      }
      let graphicsLayer = new GraphicsLayer({
        id: layerId,
        elevationInfo: 'on-the-ground',
        title: '界桩'
      })
      let markerSymbol = {
        type: 'picture-marker', // autocasts as new SimpleMarkerSymbol()
        url: presetBm3,
        height: 16,
        width: 16,
        outline: { // autocasts as new SimpleLineSymbol()
          color: [255, 255, 255],
          width: 1
        }
      }

      if (pointArray && pointArray.length > 0) {
        for (let i = 0; i < pointArray.length; i++) {
          point = {
            type: 'point', // autocasts as new Point()
            longitude: pointArray[i].longitude,
            latitude: pointArray[i].latitude
          }
          let lineAtt = {
            lpId: pointArray[i].id,
            jzNumber: pointArray[i].jzNumber,
            placeName: pointArray[i].placeName,
            longitude: pointArray[i].longitude,
            latitude: pointArray[i].latitude
          }
          pointGraphic = new Graphic({
            geometry: point,
            symbol: markerSymbol,
            attributes: lineAtt,
            popupTemplate: this.popupTemplate
          })
          graphicsLayer.add(pointGraphic)
        }
      }
      this.map.add(graphicsLayer)
    },
    /**
     * 双击表格定位
     * @param lon
     * @param lat
     */
    async ondbclickDingwei (lon, lat, id) {
      if (this.loadComplete === true) {
        // 获取拐点的图层
        let graphicLayer = this.map.findLayerById('inflectionPoint')
        if (graphicLayer !== undefined) {
          this.view.center = [lon, lat]
          this.view.zoom = 12
          let markerSymbol = {
            type: 'picture-marker', // autocasts as new SimpleMarkerSymbol()
            url: '',
            height: 16,
            width: 16,
            outline: { // autocasts as new SimpleLineSymbol()
              color: [255, 255, 255],
              width: 1
            }
          }
          let Graphic = await arcgisPackage.Graphic
          // 获取图层的graphic
          let graphics = graphicLayer.graphics.items
          // 定义临时点数组
          let tempGraphicArr = []
          // 遍历获取点击的拐点
          for (let i = 0; i < graphics.length; i++) {
            let tempGraphic = null
            /* 将选中的点改为激活的样式 */
            if (graphics[i].attributes.lpId === id) {
              // 重新定义当前graphic
              let point = {
                type: 'point', // autocasts as new Point()
                longitude: graphics[i].geometry.longitude,
                latitude: graphics[i].geometry.latitude
              }
              let lineAtt = {
                lpId: graphics[i].attributes.lpId,
                jzNumber: graphics[i].attributes.jzNumber,
                placeName: graphics[i].attributes.placeName,
                longitude: graphics[i].attributes.longitude,
                latitude: graphics[i].attributes.latitude,
                isActive: false // 用于判断当前拐点是否是激活状态
              }
              markerSymbol.url = collSign3
              tempGraphic = new Graphic({
                geometry: point,
                symbol: markerSymbol,
                attributes: lineAtt,
                popupTemplate: this.popupTemplate
              })
            } else {
              // 重新定义当前graphic
              let point = {
                type: 'point', // autocasts as new Point()
                longitude: graphics[i].geometry.longitude,
                latitude: graphics[i].geometry.latitude
              }
              markerSymbol.url = presetBm3
              // 定义点的属性
              let lineAtt = {
                lpId: graphics[i].attributes.lpId,
                jzNumber: graphics[i].attributes.jzNumber,
                placeName: graphics[i].attributes.placeName,
                longitude: graphics[i].attributes.longitude,
                latitude: graphics[i].attributes.latitude,
                isActive: false // 用于判断当前拐点是否是激活状态
              }
              tempGraphic = new Graphic({
                geometry: point,
                symbol: markerSymbol,
                attributes: lineAtt,
                popupTemplate: this.popupTemplate
              })
            }
            // 将临时点放入临时点坐标中
            tempGraphicArr.push(tempGraphic)
          }
          // 清空图层
          graphicLayer.removeAll()
          tempGraphicArr.forEach(val => {
            graphicLayer.add(val)
          })
        }
      }
    },
    /**
     * @Description:测量工具点击事件
     * @author 张淼
     * @date 2019/4/11
     */
    async clickToolbar (type) {
      if (this.type === type) {
        type = null
      }
      this.type = type
      if (this.activeWidget) {
        this.view.ui.remove(this.activeWidget)
        this.activeWidget.destroy()
        this.activeWidget = null
      }
      switch (type) {
        case 'distance':
          let DistanceMeasurement2D = await arcgisPackage.DistanceMeasurement2D
          this.activeWidget = new DistanceMeasurement2D({
            view: this.view
          })
          this.activeWidget.viewModel.newMeasurement()
          this.view.ui.add(this.activeWidget, 'top-right')
          break
        case 'area':
          let AreaMeasurement2D = await arcgisPackage.AreaMeasurement2D
          this.activeWidget = new AreaMeasurement2D({
            view: this.view
          })
          this.activeWidget.viewModel.newMeasurement()
          this.view.ui.add(this.activeWidget, 'top-right')
          break
      }
    }
  }
}

</script>

<style lang="scss" scoped rel="stylesheet/scss">
  #viewDiv{
    height:100%;
  }
</style>
